@import url(https://fonts.googleapis.com/css?family=Open+Sans|Poppins:400,700&display=swap);
html,
body {
  height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'OpenSans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
  display: flex;
}

/* Desktop */
@media screen and (min-width: 768px) {
  body {
    background: url(/huddle-landing-page/static/media/bg-desktop.bbf1c96b.svg);
  }
}

/* Mobile */
@media screen and (max-width: 767px) {
  body {
    background: url(/huddle-landing-page/static/media/bg-mobile.3b9d812e.svg);
  }
}

body {
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: right top;
  background-color: var(--color-violet);
}

:root {
  --color-violet: #674bb1;
}

